import React from "react";
import {MenuBtn} from './general/style/style';


class MenuNavButton extends React.Component {

  onNavBtnClick = (e) =>{    
    
    if(typeof(logEvent)!="undefined" && typeof(newsHuntAction)!=='undefined'){
      logEvent("explorebutton_click", "NEWS", { referrer_id: HASHTAG_ID, referrer: "webitem_topic", type: e.target.innerText.toLowerCase()});
    }

    if(this.props.navLink && this.props.navLink.startsWith("#") && this.props.index>=0 && typeof(this.props.navChangeHandler)!="undefined"){
      this.props.navChangeHandler(this.props.index);
      e.currentTarget.href = this.props.navLink;
      return;
    }
    
    const resourceIdentifier = this.props.resourceIdentifier? this.props.resourceIdentifier.replace('<lang>',this.props.lang||"en"):null;
        
    try{
      if(typeof(newsHuntAction)!=='undefined' && typeof(callEvent)!="undefined" && resourceIdentifier){
        typeof(overlayWithLoader)!="undefined" && overlayWithLoader();
        typeof(this.props.navChangeHandler)!="undefined" && typeof(this.props.index)!="undefined" && this.props.navChangeHandler(this.props.index)
        callEvent("nhcommand://openwebitemfromresource:webItemRI=" +resourceIdentifier);
      }else if(this.props.navLink){
          typeof(overlayWithLoader)!="undefined" && overlayWithLoader();
          typeof(this.props.navChangeHandler)!="undefined" && typeof(this.props.index)!="undefined" && this.props.navChangeHandler(this.props.index)
          e.currentTarget.href = this.props.navLink+`?lang=${this.props.lang??"en"}`;
      }
    }catch(err){
      console.log("MenuNavButton: ",err);
    }
  }

  render() {
    return (
      <MenuBtn radius="100px"
       onClick={(e)=>this.onNavBtnClick(e)}
       className={this.props.active == "active" ? "menu active" : "menu"}>  {this.props.buttonName}</MenuBtn>
    );
  }
}

export default MenuNavButton;






